<template> 
  <div> 
    <div class="goods"> 
      <div class="menu-wrapper"> 
        <ul>
          <!-- current -->
          <li class="menu-item" v-for="(menu,index) in goods" :key="index" 
          :class="{current: index === currentIndex}" @click="clickMenuItem(index)"> 
            <span class="text bottom-border-1px"> 
              <img class="icon" :src="menu.icon" v-if="menu.icon"> 
              {{menu.name}} 
            </span>
          </li> 
        </ul> 
      </div> 
      <div class="foods-wrapper"> 
        <ul ref="foodsUl">
          <li class="food-list-hook" v-for="(menu, index) in goods" :key="index" > 
            <h1 class="title">{{menu.name}}</h1> 
            <ul>
              <li class="food-item bottom-border-1px" v-for="(food, index) in menu.foods" :key="index" @click="showFood(food)"> 
                <div class="icon"> 
                  <img width="57" height="57" :src="food.icon"> 
                </div> 
                <div class="content"> 
                  <h2 class="name">{{food.name}}</h2> 
                  <p class="desc">{{food.description | ellipsis}}</p> 
                  <div class="extra"> 
                    <span class="count">月售 {{food.sellCount}} 份</span> 
                    <span>好评率 {{food.rating}}%</span>
                  </div> 
                  <div class="price"> 
                    <span class="now">￥{{food.price}}</span> 
                    <span class="old" v-if="food.oldPrice">￥{{food.oldPrice}}</span> 
                  </div> 
                  <div class="cartcontrol-wrapper"> 
                    <CartControl :food="food"/>  
                  </div> 
                </div> 
              </li> 
              
            </ul> 
          </li> 
          <!-- food-list-hook -->
        </ul> 
      </div> 
      <ShopCart />
    </div> 
    <Food :food="food" ref="food"/>
  </div> 
</template> 

<script>
import BScroll from 'better-scroll'
import {mapState} from 'vuex'
import CartControl from '../../../components/CartControl/CartControl.vue'
import Food from '../../../components/Food/Food.vue'
import ShopCart from '../../../components/ShopCart/ShopCart.vue'
export default {
  data() {
    return {
      scrollY: 0,
      tops: [],
      food:{}
    }
  },
  mounted() {
    this.$store.dispatch('getShopGoods',() => {
      this.$nextTick(() => {  //  列表数据更新显示后执行
        this._initScroll()
        this._initTops()
      })
    })
  },
  computed: {
    ...mapState(['goods']),

    // 计算得到当前分类的下标
    currentIndex() {
      // 得到条件数据
      const {scrollY,tops} = this
      // 根据条件进行加工
      // findIndex() 方法为数组中的每个元素都调用一次函数执行
      // indexOf() 方法可返回数组中某个指定的元素位置。
      const index = tops.findIndex((top, index) => {
        return scrollY >= top && scrollY < tops[index+1]
      })
      return index
    }
    
  },
  methods: {
    // 初始化滚动
    _initScroll(){
        // 列表显示后创建
        // 左边menu
        new BScroll('.menu-wrapper',{
          click: true
        })
        // 右边foods
        this.foodsScroll = new BScroll('.foods-wrapper',{
          probeType: 1,      //因为惯性滑动不会触发
          click: true
        })

        // 给右侧列表绑定scroll监听
        this.foodsScroll.on('scroll',({x,y}) => {
          this.scrollY = Math.abs(y)
          console.log(x, this.scrollY)
        })
        // 给右侧列表绑定scrollEnd监听
        this.foodsScroll.on('scrollEnd',({x,y}) => {
          this.scrollY = Math.abs(y)
          console.log("scrollEnd: ",x,this.scrollY)
        })
    },
    // 初始化Tops
    _initTops(){
      // 1.初始化tops
      const tops = []
      var top = 0
      tops.push(top)
      // 2.收集Tops
      // ↓返回假数组
      const lis = this.$refs.foodsUl.getElementsByClassName('food-list-hook') 
      // ↓转成真数组
      Array.prototype.slice.call(lis).forEach(li => {
        top += li.scrollHeight
        tops.push(top)
      })

      // 3.更新数据
      this.tops = tops
    },
    // 通过点击菜单滑动到指定位置
    clickMenuItem(index) {
      const y = this.tops[index]
      this.scrollY = y
      this.foodsScroll.scrollTo(0, -y,300)
    },

    showFood(food){
      this.food = food
      this.$refs.food.toogleShow()
    }
    
  },
  filters: {
    ellipsis (value) {
      if (value.length >15) {
        return value.slice(0,15) + '...'
      }
    }
  },
  components: {
    CartControl,
    Food,
    ShopCart
  },
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../../common/stylus/mixins.styl"
  .goods
    display: flex
    position: absolute
    top: 195px
    bottom: 46px
    width: 100%
    background: #fff
    overflow: hidden
    .menu-wrapper
      flex: 0 0 80px
      width: 80px
      background: #f3f5f7
      .menu-item
        display: table
        height: 54px
        width: 56px
        padding: 0 12px
        line-height: 14px
        &.current
          position: relative
          z-index: 10
          margin-top: -1px
          background: #fff
          color: $green
          font-weight: 700
          .text
            border-none()
        .icon
          display: inline-block
          vertical-align: top
          width: 12px
          height: 12px
          margin-right: 2px
          background-size: 12px 12px
          background-repeat: no-repeat
        .text
          display: table-cell
          width: 56px
          vertical-align: middle
          bottom-border-1px(rgba(7, 17, 27, 0.1))
          font-size: 12px
    .foods-wrapper
      flex: 1
      .title
        padding-left: 14px
        height: 26px
        line-height: 26px
        border-left: 2px solid #d9dde1
        font-size: 12px
        color: rgb(147, 153, 159)
        background: #f3f5f7
      .food-item
        display: flex
        margin: 18px
        padding-bottom: 18px
        bottom-border-1px(rgba(7, 17, 27, 0.1))
        &:last-child
          border-none()
          margin-bottom: 0
        .icon
          flex: 0 0 57px
          margin-right: 10px
        .content
          flex: 1
          .name
            margin: 2px 0 8px 0
            height: 14px
            line-height: 14px
            font-size: 14px
            color: rgb(7, 17, 27)
          .desc, .extra
            line-height: 10px
            font-size: 10px
            color: rgb(147, 153, 159)
          .desc
            line-height: 12px
            margin-bottom: 8px
          .extra
            .count
              margin-right: 12px
          .price
            font-weight: 700
            line-height: 24px
            .now
              margin-right: 8px
              font-size: 14px
              color: rgb(240, 20, 20)
            .old
              text-decoration: line-through
              font-size: 10px
              color: rgb(147, 153, 159)
          .cartcontrol-wrapper
            position: absolute
            right: 0
            bottom: 12px
</style>


